	module.exports = {"data":{"post":{"html":"<p>I've been using the online feed reader <a href=\"http://www.bloglines.com/\">Bloglines</a> for few weeks now (replacing my previous favorite <a href=\"http://minutillo.com/steve/feedonfeeds/\">Feed on Feeds</a>, which was hogging the resources on my web server). It's fast and very usable, but I was <a href=\"http://www.silent-penguin.com/archives/001874.html\" title=\"The Silent Penguin: Bloglines with new functionality\">wishing for the same feature as Matthew</a>, namely the feeds with new entries be sorted to the top. Otherwise, if you have several hundred unread entries (like, if you went to sleep for the night), you quickly become lost and the temptation is great to Mark All Read and start over. Lo and behold, Bloglines has added just this feature, <a href=\"http://www.bloglines.com/about/news#63\">and many more</a>. And it's still free, although I'm sure that now that it's become indispensible, it won't be for long.</p>","fields":{"title":"","date":"08 Jul 2004","url":"https://papascott.de/2004-07-08-bloglines-ueber-alles/"}}},"pathContext":{"slug":"/2004-07-08-bloglines-ueber-alles/","prev":{"fields":{"title":"Thomas Klestil","date":"07 Jul 2004","slug":"/2004-07-07-thomas-klestil/"}},"next":{"fields":{"title":"One Individual's Distorted Point of View","date":"08 Jul 2004","slug":"/2004-07-08-one-individuals-distorted-point-of-view/"}}}}

